import 'es6-promise/auto'
import Vue from 'vue'
import Axios from 'axios'
import Common from './components/common.js';

Vue.component('common', Common);

new Vue({
    el: '#scope-board-update',
    data: function() {
        return {
            bbsContentViewModel: {
                listBBS: [],
                detailContent: {bbs_id:''},
                listCategory:[]
            }
        }
    },
    mounted: function() {
        console.log("mounted [board update]");
        try {
            pageLoad(this);            
        } catch (e) {
            console.log(e);
        }
    },
    methods: {
        fnGetInit: function(paramBbsID,paramSeq){
            let vm = this;

            $("#mydropzone").dropzone();
            $('#xp-summernote').summernote({
                height: 220,
                minHeight: null,
                maxHeight: null,
                focus: false ,
                lang: 'ko-KR'
            });

            vm.bbsContentViewModel.detailContent.bbsID=paramBbsID;
            if(paramBbsID==null)
                paramBbsID = vm.bbsContentViewModel.bbsId;

            Axios.post("/api/admin/board/content/detail",{id:paramBbsID,paramSeq:paramSeq})
            .then(function(response) {
                let bbsNm = paramBbsID=='BBS_001'?'notice':'general';
                vm.bbsContentViewModel.bbsTitle=paramBbsID=='BBS_001'?'':'';
                vm.bbsContentViewModel.detailContent=response.data.detailContent;
                vm.bbsContentViewModel.listCategory=response.data.listCategory;
                vm.bbsContentViewModel.detailContent.create_date=Common.fnConvertDateStr(response.data.detailContent.create_date);
                vm.bbsContentViewModel.detailContent.updateUrl = "/board/"+bbsNm+"/update/"+vm.bbsContentViewModel.detailContent.bbs_id+"/"+vm.bbsContentViewModel.detailContent.bbs_seq;
                vm.bbsContentViewModel.detailContent.listUrl = "/board/"+bbsNm;
                $('#xp-summernote').summernote({
                height: 220,
                minHeight: null,
                maxHeight: null,
                focus: false ,
                lang: 'ko-KR',
                callbacks: {
                    onPaste: function (e) {
                    var bufferText = ((e.originalEvent || e).clipboardData || window.clipboardData).getData('Text');
                    e.preventDefault();
                    //Firefox fix
                    setTimeout(function () {
                        document.execCommand('insertText', false, bufferText);
                    }, 10);
                    }
                }
                });
    
                //$("#smartEditor").val($('#xp-summernote').summernote('code', vm.bbsContentViewModel.bbs_cont));
                $('#xp-summernote').summernote('code', vm.bbsContentViewModel.detailContent.bbs_cont);                
            });
        }
        ,fnFileDownload: function(paramSeq){
            Common.fnOnFileDownload(paramSeq);
        }
        ,fnOnUpdateSubmit: function () {
            let vm = this;
            if(vm.bbsContentViewModel.detailContent.bbs_cont_title == null || vm.bbsContentViewModel.detailContent.bbs_cont_title == ""){
              swal(" ");
              return false;
            }
            
            $("#smartEditor").val($('#xp-summernote').summernote('code'));
            $("#file_grp_id").val($('#fileGroup').val());
    
            let fileUploadType = ['doc','xlsx','hwp','txt','jpg','gif','png','jpeg','xml','pdf','ppt','pptx','docx','xls','bmp','zip'];
            let fileBoo = true;
            for(let i=0; i<document.mainForm.uploadFiles.length; i++){
                let fileObj = document.mainForm.uploadFiles[i];
                if(fileObj.files.length>0){
                    let fileName = fileObj.files[0].name;
                    let files = fileName.split('.');
                    let fileExt = files[files.length-1];
                    if(fileUploadType.indexOf(fileExt) == -1){
                        fileBoo = false;
                    }
                }
            }
            if(fileBoo == false){
                swal('   .');
                return;
            }
            let bbsNm = vm.bbsContentViewModel.detailContent.bbs_id=='BBS_001'?'notice':'general';
            document.forms['mainForm'].action="/board/"+bbsNm+"/update";
            document.forms['mainForm'].submit();
          }
        
    }
});


